header {
  background-color: #52de60;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  border: 1px solid black;
  padding: 0 100px;
}

.header-items {
  display: flex;
  justify-content: space-between;
  width: 30%;
}

.header-icons {
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.hero-wrapper {
  background-color: #52de60;
  padding: 0px 100px;
  background-image: url(./images/signs.png);
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
}

.hero-banner {
  display: flex;
  justify-content: flex-end;
  margin-top: 128px;
  width: 50%;
  border: 1px solid black;
}

.hero-car {
  display: flex;
  justify-content: flex-start;
  margin-top: 128px;
  border: 1px solid black;
}

.hero-banner > img {
  height: 63%;
  width: auto;
}

.hero-car > img {
  height: 75%;
  width: auto;
}

.instruction-wrapper {
  background-color: #52de60;
  padding: 0px 100px;
  height: 60vh;
  display: flex;
  justify-content: space-between;
}

.instruction-image {
  width: 45%;
  align-self: flex-start;
  display: flex;
  justify-content: flex-start;
}


.instruction-image > img {
  width: 80%;
}

.instruction-call {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;;
}

.call-button {
  display: flex;
  align-items: center;
}

.actions-wrapper {
  padding: 0px 100px;

  background-color: #52de60;
height: 40vh;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.actions-card {
  border: 1px solid red;
  border-radius: 15px;
  width: 400px;
  height: 200px;
}

.bio {
  background-image: url(./images/bio.png);
  background-size: cover;
  background-repeat: no-repeat;
}

.practice {
  background-image: url(./images/practice.png);
  background-size: cover;
  background-repeat: no-repeat;
}

.instruction {
  background-image: url(./images/Instruction.png);
  background-size: cover;
  background-repeat: no-repeat;
}

.manual-wrapper {
  display: flex;
}


.content-body {
  background-image: url(./images/school.png);
  background-size: cover;
  background-repeat: no-repeat;
  width: 420px;
  height: 320px;
}

footer {
  background-color: #52de60;
  padding: 0px 100px;
  display: flex;
  justify-content: space-between;
}